<template>
    <div class="content-wrapper">
      <div class="loading-container" v-if="isLoading">
        <div class="loading-wrapper">
          <lu-loading/>
        </div>
      </div>
      <lu-scroll
        ref="scroll"
        @scroll-end = "scrollEnd"
      >
        <div class="content">
          <div class="pic" v-if="content.banner">
            <a href="##" class="pic-link">
              <img :src="content.banner.picUrl" alt="" class="pic-img" @load="updateScroll">
            </a>
          </div>
          <div
            class="section"
            v-for="(section, index) in content.data"
            :key="index"
          >
            <h4 class="section-item">{{section.name}}</h4>
            <ul class="section-list">
              <li
                class="section-item"
                v-for="(item, i) in section.itemList"
                :key="i"
              >
                <a href="##" class="section-link">
                  <p class="section-pic" v-if="item.picUrl">
                    <img v-lazy="item.picUrl" alt="" class="section-img">
                  </p>
                  <p class="section-name">{{item.name}}</p>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </lu-scroll>
      <div class="g-backtop-container">
        <lu-backtop @backtop="backToTop" :visible="isBacktopVisible"/>
      </div>
    </div>
</template>

<script>
  import LuLoading from '@/base/loading';
  import LuScroll from '@/base/scroll';
  import LuBacktop from '@/base/backtop';
  import {getCategoryContent} from '@/api/category.js';
  import storage from '@/assets/js/storage.js';
  import {CATEGORY_CONTENT_KEY, CATEGORY_CONTENT_UPDATE_TIME_INTERVAL} from './config';
  export default {
    name: 'CategoryContent',
    components: {
      LuLoading,
      LuScroll,
      LuBacktop
    },
    data() {
      return {
        content: {},
        isBacktopVisible: false,
        isLoading: false
      };
    },
    methods: {
      backToTop() {
        this.$refs.scroll && this.$refs.scroll.scrollToTop();
      },
      updateScroll() {
        this.$refs.scroll && this.$refs.scroll.update();
      },
      scrollEnd(translate, scroll) {
        this.isBacktopVisible = translate < 0 && -translate > scroll.height;
      },
      getContent(id) {
        let contents = storage.get(CATEGORY_CONTENT_KEY);
        let updateTime;
        const curTime = new Date().getTime();
        if (contents && contents[id]) { // localstorage中有数据
          updateTime = contents[id].updateTime || 0;
          if (curTime - updateTime <= CATEGORY_CONTENT_UPDATE_TIME_INTERVAL) { // 缓存还未失效
            return this.getContentByLocalstorage(contents[id]);
          } else { // 缓存失效从服务端获取
            return this.getContentByHTTP(id).then(() => { // 获取数据之后更新localstorage
              this.updateLocalStorage(contents, id, curTime);
            });
          }
        } else { // localstorage 中无数据
          return this.getContentByHTTP(id).then(() => {
            this.updateLocalStorage(contents, id, curTime);
          });
        }
      },
      getContentByHTTP(id) {
        return getCategoryContent(id).then(data => {
          return new Promise(resolve => {
            if (data) {
              this.content = data;
              resolve();
            }
          });
        });
      },
      getContentByLocalstorage(content) {
        this.content = content.data;
        return Promise.resolve();
      },
      updateLocalStorage(contents, id, curTime) {
        contents = contents || {};
        contents[id] = {};
        contents[id].data = this.content;
        contents[id].updateTime = curTime;
        storage.set(CATEGORY_CONTENT_KEY, contents);
      }
    },
    watch: {
      curId(id) {
        this.isLoading = true;
        // 获取完数据之后将loading组件隐藏
        this.getContent(id).then(() => {
          this.isLoading = false;
        });
      }
    },
    props: {
      curId: {
        type: String,
        default: ''
      }
    }
  };
</script>
<style lang="scss" scoped>
@import '~@/assets/scss/mixins';
  .content-wrapper{
    position: relative;
    height: 100%;
  }
  .loading-container{
    position: absolute;
    top: 0;
    left: 0;
    z-index: $category-popup-z-index;
    @include flex-center();
    width: 100%;
    height: 100%;
    .mine-loading{
      color: #fff;
      font-size: 14px;
    }
  }
  .loading-wrapper{
    width: 50%;
    padding: 30px 0;
    background-color: $modal-bgc;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
  }
.content {
  padding: 10px;
}

.pic {
  margin-bottom: 12px;

  &-link {
    display: block;
  }

  &-img {
    width: 100%;
  }
}

.section {
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }

  &-title {
    height: 28px;
    line-height: 28px;
    color: #080808;
    font-weight: bold;
  }

  &-list {
    display: flex;
    flex-wrap: wrap;
    background-color: #fff;
    padding: 10px 10px 0;
  }

  &-item {
    width: (100% / 3);
  }

  &-link {
    display: block;
  }

  &-pic {
    position: relative;
    width: 80%;
    padding-bottom: 80%;
    margin: 0 auto;
  }

  &-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &-name {
    height: 36px;
    line-height: 36px;
    text-align: center;
    @include ellipsis();
  }
}
.g-backtop-container {
  bottom: 10px;
}
</style>



