<template>
  <lu-navbar class="header">
    <lu-search-box
      slot="center"
      placeholder="情人节巨献"
      @query="getQuery"
      fake
      @click.native="goToSearch"
    ></lu-search-box>
    <i class="iconfont icon-msg" slot="right"></i>
  </lu-navbar>
</template>

<script>
  import LuNavbar from '@/base/navbar';
  import LuSearchBox from '@/base/search-box';
  export default {
    name: 'CategoryHeader',
    components: {
      LuNavbar,
      LuSearchBox
    },
    methods: {
      goToSearch() {
        this.$router.push('/search');
      },
      getQuery(query) {
        console.log(query);
      }
    }
  };
</script>
<style lang="scss" scoped>
@import '~@/assets/scss/mixins';
  .header{
    &.mine-navbar{
      background-color: $header-bgc-translucent;
    }
    .iconfont{
      color: $icon-color-default;
      font-size: $icon-font-size;
    }
  }
</style>



