<template>
  <transition name="lu-backtop">
    <a href="##"
       class="lu-backtop"
       v-show="visible"
       @click="backToTop"
    >
      <i class="iconfont icon-backtop"></i>
    </a>
  </transition>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'LuBacktop',
    props: {
      visible: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      backToTop(event) {
        event.preventDefault();
        this.$emit('backtop');
      }
    }
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";
  .lu-backtop{
    overflow: hidden;
    @include flex-center();
    width: 45px;
    height: 45px;
    background-color: rgba(0, 0, 0, .6);
    border: none;
    border-radius: 50%;
    .iconfont{
      color: #fff;
      font-size: 38px;
    }
  }
  .lu-backtop{
    &-enter-active,
    &-laeve-active{
      -webkit-transition: opacity 0.4s;
      -moz-transition: opacity 0.4s;
      -ms-transition: opacity 0.4s;
      -o-transition: opacity 0.4s;
      transition: opacity 0.4s;
    }
    &-enter,
    &-leave-to{
      opacity: 0;
    }
  }
</style>



