<template>
    <lu-scroll :scrollbar="false">
      <ul class="tab">
        <li
          class="tab-item"
          :class="{'tab-item-active': item.id === curId}"
          v-for="(item, index) in items"
          :key="index"
          @click="switchTab(item.id)"
        >{{item.name}}</li>
      </ul>
    </lu-scroll>
</template>

<script>
  import LuScroll from '@/base/scroll';
  import {categoryNames} from './config.js';
  export default {
    name: 'CategoryTab',
    components: {
      LuScroll
    },
    data() {
      return {
        curId: ''
      };
    },
    created() {
      this.init();
      this.switchTab(this.items[0].id);
    },
    methods: {
      init() {
        this.items = categoryNames;
      },
      switchTab(id) {
        if (this.curId === id) {
          return;
        }
        this.curId = id;
        this.$emit('switch-tab', id);
      }
    }
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";
  $tab-item-height: 46px;
  .tab{
    width: 100%;
    &-item{
      height: $tab-item-height;
      background: #fff;
      border-right: 1px solid $border-color;
      border-bottom: 1px solid $border-color;
      color: #080808;
      font-size: $font-size-l;
      font-weight: bold;
      text-align: center;
      line-height: $tab-item-height;
      @include ellipsis();
      &:last-child{
        border-bottom: none;
      }
    }
    &-item-active{
      border-bottom: none;
      border-right: none;
      background-color: #f23030;
    }
  }
</style>



